
.about-container {
    margin-bottom: auto;
    min-height: 100vh;
    width: 100vw;
    background-color: rgb(236, 232, 232);
    display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: center;
	align-items: center;
    align-content: center;
    overflow-y: auto;
}

.about-header {
    margin-top: 2px;
    height: 30px;
    width: 100vw;
    background: linear-gradient(0turn,#5077be,#6495ed);
    display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: space-around;
	align-items: baseline;
	align-content: stretch;
}

.about-header-item {
    width: 50px;
    height: 30px;
}

div.about-header a {
    color: #555;
    width: 40px;
}

div.about-header a:hover{
    border-bottom: 2px solid #3c598e;
}
div.about-header .a-selected{
    color: #fff;
}

::-webkit-scrollbar {
    width: 20px;
  }

    @media (max-width: 700px){
    .about-container {
        flex-direction: column;

    }
}
